.LOgo_ICon {
  height: auto;
  width: 45px;
  color: #fff;
  padding: 5px;
  background-color: #057c71;
  border-radius: 20px
}

.App {
  text-align: center
}

.App-logo {
  height: 40vmin;
  pointer-events: none
}

@media (prefers-reduced-motion:no-preference) {
  .App-logo {
    animation: a infinite 20s linear
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #fff
}

.App-link {
  color: #61dafb
}

@keyframes a {
  0% {
    transform: rotate(0deg)
  }

  to {
    transform: rotate(1turn)
  }
}